/* purgecss start ignore */

.lc-input-color-picker {
    width: 24px;
    height: 24px;
    background-image: linear-gradient(45deg, #e2e2e2 25%, transparent 25%),
      linear-gradient(-45deg, #e2e2e2 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #e2e2e2 75%),
      linear-gradient(-45deg, transparent 75%, #e2e2e2 75%);
    background-size: 8px 8px;
    background-position: 0 0, 0 4px, 4px -4px, -4px 0px;
    border-radius: 2px;
  }

.lc-input-color-picker-input {
      width: 24px;
      height: 24px;
      margin: -4px -2px;
      padding: 0;
      border: none;
      box-sizing: border-box;
      border-radius: 2px !important;
      overflow: hidden;
      border-width: 1px;
      border-style: solid;
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
      margin: 0;
      outline: none;
    }

.lc-input-color-picker-input:focus {
        --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
        --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
        box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
        --tw-ring-opacity: 1;
        --tw-ring-color: rgba(var(--lc-color-primary), var(--tw-ring-opacity));
      }

.lc-input-color-picker-input::-webkit-color-swatch {
        width: 24px;
        height: 24px;
        margin: -4px -2px;
        padding: 0;
        border: none;
        box-sizing: border-box;
        border-radius: 2px !important;
        overflow: hidden;
      }

.lc-input-color-picker-input::-moz-color-swatch {
        width: 24px;
        height: 24px;
        margin: -4px -2px;
        padding: 0;
        border: none;
        box-sizing: border-box;
        border-radius: 2px !important;
        overflow: hidden;
      }

.lc-input-color-invalid-hex input[type='color'] {
      background-color: transparent;
    }

.lc-input-color-invalid-hex input[type='color']::-webkit-color-swatch {
        opacity: 0;
      }

.lc-input-color-invalid-hex input[type='color']::-moz-color-swatch {
        opacity: 0;
      }

/* purgecss end ignore */
